"use client";

import { FormEvent, useMemo, useState } from "react";
import { getSupabaseClient } from "@/lib/supabase/client";

export default function EmployersPage() {
  const supabase = useMemo(() => getSupabaseClient(), []);
  const [companyName, setCompanyName] = useState("");
  const [contactName, setContactName] = useState("");
  const [email, setEmail] = useState("");
  const [submitted, setSubmitted] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = async (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setError(null);
    setSubmitted(false);
    const { error: insertError } = await supabase.from("employer_intake").insert({
      company_name: companyName,
      contact_name: contactName,
      email,
    });
    if (insertError) {
      setError(insertError.message);
      return;
    }
    setSubmitted(true);
    setCompanyName("");
    setContactName("");
    setEmail("");
  };

  return (
    <section className="families-auth">
      <div className="families-auth-inner">
        <div className="families-hero">
          <p className="eyebrow">Employers</p>
          <h1>Launch ShiftSitter for your team</h1>
          <p className="lead">
            Intake to enable ShiftSitter for employees working shifts, nights, or weekends.
          </p>
          <ul className="families-bullets">
            <li>Built for operations, retail, and plant teams.</li>
            <li>We coordinate employee verification and clear agreements.</li>
            <li>Data is stored in Supabase (employer_intake) for follow-up.</li>
          </ul>
        </div>

        <div className="families-card">
          <div className="card-header">
            <h2>Employer interest form</h2>
            <p className="muted">We’ll reach out to enable your employees.</p>
          </div>

          <form onSubmit={handleSubmit} className="form-stack">
            <div className="form-field">
              <label htmlFor="companyName">Company name</label>
              <input
                id="companyName"
                type="text"
                placeholder="Acme Manufacturing"
                value={companyName}
                onChange={(e) => setCompanyName(e.target.value)}
                className="ss-input"
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="contactName">Contact name</label>
              <input
                id="contactName"
                type="text"
                placeholder="Alex Rivera"
                value={contactName}
                onChange={(e) => setContactName(e.target.value)}
                className="ss-input"
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="email">Contact email</label>
              <input
                id="email"
                type="email"
                placeholder="alex@acme.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="ss-input"
                required
              />
            </div>

            {error && <p className="form-error">{error}</p>}
            {submitted && <p className="success">Thanks—your intake was received.</p>}

            <div className="actions">
              <button type="submit" className="ss-btn w-100">
                Submit interest
              </button>
            </div>
          </form>
        </div>
      </div>
    </section>
  );
}
